import { WelcomeLayout } from 'shared/layouts/WelcomeLayout';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { useDispatch } from 'react-redux';
import { KntInput } from 'shared/components/Fields';
import Actions from 'store/actions';
import { AxiosError } from 'axios';
import { useHistory, useLocation } from 'react-router-dom';
import qs from 'query-string';
import { useLogout, useNotification } from 'shared/hooks';
import { Helmet } from 'react-helmet';
import React from 'react';
const FormRequest = Actions.entity.formRequest;

const validationSchema = yup.object({
	username: yup.string().email('Enter a valid email').required('Email is required'),
	token: yup.string().required('Confirmation code is required'),
	new_password: yup
		.string()
		.strict(true)
		.trim('Password cannot be blank')
		.min(6, 'Password should be of minimum 6 characters length')
		.required('Password is required'),
	passwordConfirmation: yup
		.string()
		.oneOf([yup.ref('new_password')], 'Passwords are not the same!')
		.required('Password confirmation is required!')
});

function ChangePassword() {
	useLogout({ isChangePassword: true });
	const dispatch = useDispatch();
	const history = useHistory();
	const location = useLocation();
	const { notifVisible, setNotifVisible, handleNotificationClose, Notification } =
		useNotification();
	const query = qs.parse(location.search) || { username: '' };
	const formik = useFormik({
		initialValues: {
			username: query.username,
			new_password: '',
			token: query.code,
			passwordConfirmation: ''
		},
		validationSchema: validationSchema,
		onSubmit: (values, { setFieldError, setSubmitting }) => {
			const filteredValues = { ...values };
			// @ts-ignore
			delete filteredValues.passwordConfirmation;

			dispatch(
				FormRequest({
					entity: 'change_password',
					name: 'change_password',
					url: '/user/change-password/',
					method: 'put',
					params: {},
					values: filteredValues,
					cb: {
						success: (data: any) => {
							setSubmitting(false);
							setNotifVisible(true);
							setTimeout(() => {
								history.push('/signin');
							}, 3000);
						},
						error: (error: AxiosError) => {
							setFieldError('token', error.response?.data?.message);

							setSubmitting(false);
						},
						// eslint-disable-next-line @typescript-eslint/no-empty-function
						finally: () => {}
					}
				})
			);
		}
	});

	// @ts-ignore
	return (
		<WelcomeLayout
			title={'Change password'}
			description={'Please, check your email for confirmation code'}
			goToPageLink={'/signin'}
			goToPage={'Have you remembered your password?'}
			goToPageLinkText={'Sign In'}
			back
			content={
				<>
					<Helmet>
						<title>KENNEKT | Change password</title>
					</Helmet>
					<form onSubmit={formik.handleSubmit}>
						<div className=" w-full font-poppins md:w-[423px]">
							<KntInput
								name={'token'}
								placeholder={'Enter confirmation code'}
								label={'Confirmation code'}
								required
								type={'tel'}
								onChange={formik.handleChange}
								//@ts-ignore
								value={formik.values.token}
								error={formik.touched.token && Boolean(formik.errors.token)}
								helperText={formik.touched.token && formik.errors.token}
							/>
							<KntInput
								name={'username'}
								placeholder={'Enter your email'}
								label={'Email address'}
								required
								onChange={formik.handleChange}
								type={'email'}
								//@ts-ignore
								value={formik.values.username!}
								error={formik.touched.username && Boolean(formik.errors.username)}
								helperText={formik.touched.username && formik.errors.username}
							/>

							<KntInput
								name={'new_password'}
								placeholder={'Password'}
								label={'Password'}
								required
								onChange={formik.handleChange}
								type={'password'}
								value={formik.values.new_password}
								error={formik.touched.new_password && Boolean(formik.errors.new_password)}
								helperText={formik.touched.new_password && formik.errors.new_password}
							/>
							<KntInput
								name={'passwordConfirmation'}
								placeholder={'Confirm password'}
								label={'Password confirmation'}
								required
								onChange={formik.handleChange}
								type={'password'}
								value={formik.values.passwordConfirmation}
								error={
									formik.touched.passwordConfirmation && Boolean(formik.errors.passwordConfirmation)
								}
								helperText={
									formik.touched.passwordConfirmation && formik.errors.passwordConfirmation
								}
								classNames={'mb-8'}
							/>
							<Notification
								severity={'info'}
								anchorOrigin={{
									vertical: 'bottom',
									horizontal: 'center'
								}}
								handleClose={handleNotificationClose}
								open={notifVisible}
								message="Your password has been changed. You will redirected to sign in page"
								autoHideDuration={3000}
							/>
							<button
								disabled={formik.isSubmitting}
								className={
									'bg-primary py-3.5 uppercase transition ' +
									'w-full rounded-lg text-center text-sm font-light text-white hover:bg-primary-dark'
								}
							>
								{formik.isSubmitting ? 'Changing password. Please wait a while' : 'Change password'}
							</button>
						</div>
					</form>
				</>
			}
		/>
	);
}

export default ChangePassword;
